/* Home */
#root {
  font-family: "Audiowide", cursive;
}
.btn {
  width: 100px;
  border: 1px solid black;
}
.purple:hover {
  color: white;
}
.btn:focus {
  outline: none;
  box-shadow: none;
}

.purple {
  background-color: purple;
  color: white;
}

.white {
  color: white;
}

.red {
  color: rgb(255, 0, 64);
}

.btn-warning {
  color: white !important;
}

.btn-play {
  width: 300px;
}

.btn-default {
  background-color: whitesmoke;
}

.coral:disabled {
  opacity: 1;
  background-color: coral;
  color: white;
}

.aqua:disabled {
  opacity: 1;
  background-color: rgba(0, 255, 255, 0.555);
  color: white;
}

.lime:disabled {
  opacity: 1;
  background-color: limegreen;
  color: white;
}

.bg-main {
  background-color: slategray;
}

.badge-1 {
  background-color: rgb(127, 80, 255);
  font-size: 13px;
  padding: 8px;
  border-radius: 5px;
  color: whitesmoke;
}

.badge-2 {
  background-color: rgb(18, 180, 32);
  font-size: 13px;
  padding: 8px;
  border-radius: 5px;
  color: whitesmoke;
}

.bg-loading {
  background: url("./2.gif");
  background-repeat: no-repeat;
}

.loading {
  width: 65px;
  height: 65px;
}

.choice:hover {
  background-color: rgb(255, 125, 78, 0.2);
}

.question-holder {
  border: solid black 2px;
  background-color: whitesmoke;
  height: 50vh;
}

.next:hover {
  background-color: tomato;
}

.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
  background-color: tomato;
}

.progress-bar {
  -webkit-transition: none !important;
  transition: none !important;
}

.btn-group .btn {
  border-top-left-radius: 5px !important;
}

.container {
  width: 75vw !important;
}

.question {
  height: 33vh;
}
